'use strict';
// Days Array
var days = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];
// Months
var months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
// Returns a string with the day of the week
var getDay = function (day) {
    var currentDate = new Date();
    return days[day !== undefined ? day : currentDate.getDay()];
};
// Returns a string with the month
var getMonth = function (month) {
    var currentDate = new Date();
    return months[month !== undefined ? month : currentDate.getMonth()];
};
// Returns format based on current time
var getFormat = function () {
    var currentDate = new Date();
    var currentHours = currentDate.getHours();
    if (currentHours > 5 && currentHours < 12) {
        return {
            currentTime: 'morning',
            greeting: 'Good Morning!',
            backgroundColor: 'efdf7e',
            dateColor: '665800'
        };
    }
    else if (currentHours >= 12 && currentHours < 17) {
        return {
            currentTime: 'afternoon',
            greeting: 'Good Afternoon!',
            backgroundColor: 'efdf7e',
            dateColor: '665800'
        };
    }
    else if (currentHours >= 17 && currentHours < 19) {
        return {
            currentTime: 'evening',
            greeting: 'Good Evening!',
            backgroundColor: 'ff851b',
            dateColor: 'ffbe84'
        };
    }
    else {
        return {
            currentTime: 'night',
            greeting: 'Good Night!',
            backgroundColor: '001f3f',
            dateColor: '0074d9'
        };
    }
};
// Converts Fahrenheit to Celsius
var celsiusFahrenheit = function (tempC) {
    var tempF = Math.floor(tempC * 1.8 + 32);
    return tempF;
};
var formatService = {
    getDay: getDay,
    getMonth: getMonth,
    getFormat: getFormat,
    celsiusFahrenheit: celsiusFahrenheit
};
export default formatService;
